@keyframes objectShow {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 500;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Magistral", sans-serif;
  background-color: #e8f9ff;
  color: #021f29;
  font-size: 16px;
  line-height: 1.3;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  position: relative;
  z-index: 1;
}

section {
  transition: opacity 1s ease;
}

a {
  color: #021f29;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.d-none-desktop {
  display: none !important;
}
@media only screen and (max-width: 991px) {
  .d-none-desktop {
    display: block !important;
  }
}

.d-none-mobile {
  display: block !important;
}
@media only screen and (max-width: 991px) {
  .d-none-mobile {
    display: none !important;
  }
}

.d-none-tablet {
  display: none !important;
}
@media only screen and (max-width: 767px) {
  .d-none-tablet {
    display: inline-flex !important;
  }
}

.d-none-mobile-tablet {
  display: inline-flex !important;
}
@media only screen and (max-width: 767px) {
  .d-none-mobile-tablet {
    display: none !important;
  }
}

[hidden] {
  display: none !important;
}

/* Mobile-menu */
[data-state=mobile-menu] {
  overflow: hidden;
}
[data-state=mobile-menu] .header__menu-wrapper {
  transform: translateX(0);
}

/* When hystmodal opened */
.hystmodal__opened .header {
  transform: translateY(-100%);
}

/* Swiper */
.swiper-nav {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 10;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  pointer-events: none;
}
@media only screen and (max-width: 767px) {
  .swiper-nav {
    display: none;
  }
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  cursor: pointer;
  pointer-events: auto;
}
.swiper-nav .swiper-btn-prev:hover svg,
.swiper-nav .swiper-btn-next:hover svg {
  color: rgba(70, 207, 255, 0.7);
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  color: #46cfff;
  transition: 0.3s ease;
}

.swiper-pagination {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 36px;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination {
    gap: 20px;
  }
}
@media only screen and (max-width: 767px) {
  .swiper-pagination {
    gap: 10px;
  }
}
.swiper-pagination__bullet {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 94px;
  height: 94px;
  background: transparent;
  opacity: 1;
  border-radius: 28px;
  border: 4px solid #46cfff;
  cursor: pointer;
  transition: 0.3s ease;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination__bullet {
    width: 70px;
    height: 70px;
    border-radius: 24px;
  }
}
@media only screen and (max-width: 767px) {
  .swiper-pagination__bullet {
    width: 50px;
    height: 50px;
    border-radius: 18px;
    border: 2px solid #46cfff;
  }
}
.swiper-pagination__bullet:hover {
  background-color: #46cfff;
}
.swiper-pagination__bullet:hover svg {
  color: #ffffff;
}
.swiper-pagination__bullet svg {
  color: #46cfff;
  width: 75%;
  height: 75%;
  transition: 0.3s ease;
}
.swiper-pagination__bullet--active {
  background-color: #46cfff;
}
.swiper-pagination__bullet--active svg {
  color: #ffffff;
}
.swiper-pagination .swiper-pagination-bullet-active {
  width: 94px;
  height: 94px;
}

/* Select2 */
.js-custom-select ~ .select2-container {
  width: 100% !important;
}

.select2-container--default .select2-selection--single {
  height: auto;
  border: 1px solid #46cfff;
  border-radius: 30px;
  padding: 28px 0;
  background-color: transparent;
  transition: background-color 0.3s ease;
}
.select2-container--default .select2-selection--single:hover {
  background-color: rgba(70, 207, 255, 0.2);
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  font-family: "IBMPlexSans", sans-serif;
  color: #021f29;
  font-weight: 300;
  font-size: 16px;
  line-height: 1;
  padding: 0 70px 0 24px;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  width: 44px;
  height: 19px;
  top: 50%;
  right: 23px;
  transform: translateY(-50%);
  transition: 0.3s ease;
}
.select2-container--default .select2-selection--single .select2-selection__arrow b {
  display: block;
  width: 100%;
  height: 100%;
  border: 0;
  background-image: url('data:image/svg+xml,<svg width="44" height="19" viewBox="0 0 44 19" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0.999999 0.999756L15.636 15.6358C19.1508 19.1505 24.8492 19.1505 28.364 15.6358L43 0.999754" stroke="%2346CFFF"/></svg>');
  background-repeat: no-repeat;
  margin: 0;
  position: unset;
}

.select2-container--open .select2-selection--single .select2-selection__arrow {
  transform: translateY(-50%) scale(1, -1);
}

.select2-dropdown {
  border: 1px solid #46cfff;
}

.select2-results__option {
  background-color: rgba(70, 207, 255, 0.2);
  padding: 15px 24px;
  font-family: "IBMPlexSans", sans-serif;
  color: #021f29;
  font-weight: 300;
  font-size: 16px;
  line-height: 1;
  transition: 0.3s ease;
}

.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable,
.select2-container--default .select2-results__option--selected {
  background-color: #46cfff;
  color: #ffffff;
}

/* Custom fields */
.ss-form-heading {
  display: none;
}
.ss-form-heading + span {
  display: block;
  margin-top: 20px;
}
.ss-form-heading + span + .ss-form-inner {
  display: none;
}

.ss-form form {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  max-width: 932px;
  margin-top: 45px;
}
@media only screen and (max-width: 767px) {
  .ss-form form {
    margin-top: 20px;
  }
}

.ss-form-item {
  display: flex;
  flex-direction: column;
  width: calc(50% - 10px);
}
@media only screen and (max-width: 767px) {
  .ss-form-item {
    width: 100%;
  }
}
.ss-form-item.ss-form-item-error .ss-form-input {
  border-color: #bf0202;
}
.ss-form-item--checkbox {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.ss-item-label {
  display: none;
}

.ss-form-input:not(.field--checkbox) {
  width: 100%;
  border: 1px solid #46cfff;
  border-radius: 30px;
  padding: 25px 24px;
  transition: 0.3s ease;
}
.ss-form-input:not(.field--checkbox)::placeholder {
  font-family: "IBMPlexSans", sans-serif;
  color: #021f29;
  font-weight: 300;
  font-size: 16px;
}
.ss-form-input:not(.field--checkbox):focus, .ss-form-input:not(.field--checkbox):hover {
  background-color: rgba(70, 207, 255, 0.2);
}

.form-item-error {
  order: 1;
  font-size: 12px;
  padding: 0 24px;
  color: #bf0202;
  margin-top: 8px;
}

.ss-form-action,
.form-item-recaptcha {
  width: 100%;
}

.ss-form-submit {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 172px;
  width: 100%;
  padding: 15px 20px;
  border-radius: 20px;
  background-color: #46cfff;
  color: #021f29;
  font-size: 18px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.3s ease;
  margin-top: 34px;
}
@media only screen and (max-width: 767px) {
  .ss-form-submit {
    margin-top: 20px;
  }
}
.ss-form-submit:hover {
  background-color: rgba(70, 207, 255, 0.7);
}

.ss-form-report {
  font-size: 30px;
  margin: 30px 0;
}
@media only screen and (max-width: 767px) {
  .ss-form-report {
    font-size: 24px;
    margin: 20px 0;
  }
}

/* Adv mark article */
.token-mark-block {
  position: relative;
  z-index: 20;
  display: block;
  max-width: 1288px;
  width: 100%;
  padding: 0 20px;
  margin: 30px auto 0;
  box-sizing: border-box;
}

.article-mark:hover + .token-info {
  opacity: 1;
  pointer-events: auto;
}

.token-info {
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 270px;
  border: 2px solid #ffffff;
  background: rgba(70, 207, 255, 0.7);
  backdrop-filter: blur(11px);
  border-radius: 2px;
  padding: 20px 10px;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
}

.token-info-item {
  display: block;
  color: #ffffff;
  font-size: 12px;
  line-height: 1.2;
}
.token-info-item a {
  color: #ffffff;
  text-decoration: underline;
}
.token-info-item a:hover {
  text-decoration: none;
}

/* Blocks */
.container {
  max-width: 1288px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 336px;
  width: 100%;
  padding: 12px 20px;
  border-radius: 16px;
  background-color: #46cfff;
  color: #021f29;
  font-size: 18px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.3s ease;
}
.button:hover {
  background-color: rgba(70, 207, 255, 0.7);
}

.button-up {
  position: fixed;
  right: 30px;
  bottom: 110px;
  z-index: 12;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 58px;
  height: 58px;
  background-color: #ffffff;
  border-radius: 12px;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
  cursor: pointer;
}
@media only screen and (max-width: 991px) {
  .button-up {
    bottom: 30px;
  }
}
@media only screen and (max-width: 767px) {
  .button-up {
    width: 40px;
    height: 40px;
  }
}
.button-up--visible {
  opacity: 1;
  pointer-events: auto;
}
.button-up:hover svg {
  transform: translateY(-3px);
}
.button-up svg {
  width: 56%;
  height: 56%;
  transition: 0.3s ease;
}

.title {
  font-weight: 400;
  font-size: 46px;
  line-height: 1;
}
@media only screen and (max-width: 1279px) {
  .title {
    font-size: calc(30px + (46 - 30) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .title {
    font-size: calc(25px + (30 - 25) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .title {
    font-size: calc(20px + (30 - 20) * (100vw - 0px) / (768 - 0));
  }
}
.title--big {
  font-size: 50px;
}
@media only screen and (max-width: 1279px) {
  .title--big {
    font-size: calc(30px + (50 - 30) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .title--big {
    font-size: calc(25px + (30 - 25) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .title--big {
    font-size: calc(20px + (30 - 20) * (100vw - 0px) / (768 - 0));
  }
}
.title span {
  color: #46cfff;
}

.hystmodal__window {
  max-width: 1040px;
  width: 100%;
  border-radius: 30px;
  overflow: hidden;
}
.hystmodal:before {
  opacity: 1;
  background-color: rgba(2, 31, 41, 0.2);
}
.hystmodal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  padding: 0;
  cursor: pointer;
  background-image: unset;
  background-size: unset;
}
.hystmodal__close:focus {
  outline: none;
  outline-offset: unset;
}
.hystmodal__close:hover svg {
  color: #46cfff;
}
.hystmodal__close svg {
  transition: 0.3s ease;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  padding: 80px 0;
  overflow: hidden;
  transition: 0.5s ease;
}
@media only screen and (max-width: 767px) {
  .header {
    padding: 40px 0;
  }
}
.header--bg {
  background-color: #ffffff;
  box-shadow: 4px 4px 34px 0 rgba(2, 31, 41, 0.2);
  padding: 40px 0;
}
.header--hide {
  transform: translateY(-100%);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.header__logos {
  display: flex;
  align-items: center;
  gap: 48px;
}
@media only screen and (max-width: 575px) {
  .header__logos {
    gap: 15px;
  }
}
.header__logo {
  transition: 0.3s ease;
}
.header__logo:hover {
  opacity: 0.7;
}
.header__logo svg {
  width: 153px;
  height: 23px;
}
@media only screen and (max-width: 575px) {
  .header__logo svg {
    width: 100px;
    height: 15px;
  }
}
.header__logo-cn {
  transition: 0.3s ease;
}
.header__logo-cn:hover {
  opacity: 0.7;
}
.header__logo-cn svg {
  width: 114px;
  height: 22px;
}
@media only screen and (max-width: 575px) {
  .header__logo-cn svg {
    width: 78px;
    height: 15px;
  }
}
.header__menu-wrapper {
  transition: 0.3s ease;
}
@media only screen and (max-width: 767px) {
  .header__menu-wrapper {
    position: fixed;
    inset: 0;
    z-index: 10;
    background-color: #e8f9ff;
    transform: translateX(100%);
  }
}
.header__menu {
  display: flex;
  align-items: center;
  gap: 42px;
}
@media only screen and (max-width: 767px) {
  .header__menu {
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }
}
.header__menu li a {
  position: relative;
  font-family: "IBMPlexSans", sans-serif;
  font-weight: 300;
}
@media only screen and (max-width: 767px) {
  .header__menu li a {
    font-weight: 700;
    font-size: 26px;
    text-transform: uppercase;
  }
}
.header__menu li a::before {
  content: "";
  position: absolute;
  width: 0;
  inset: -5px -5px -10px;
  border-radius: 5px;
  background-color: #46cfff;
  transition: width 0.2s ease, top 0.2s ease 0.2s;
}
@media only screen and (max-width: 767px) {
  .header__menu li a::before {
    width: calc(100% + 10px);
    top: 23px;
  }
}
.header__menu li a:hover::before {
  width: calc(100% + 10px);
  top: 23px;
}
.header__menu li span {
  position: relative;
}
.header__menu-close {
  position: absolute;
  top: 20px;
  right: 20px;
  display: none;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
@media only screen and (max-width: 767px) {
  .header__menu-close {
    display: flex;
  }
}
.header__menu-close svg {
  color: #021f29;
}
.header__burger {
  display: none;
  align-items: center;
  justify-content: center;
  min-width: 30px;
  max-width: 30px;
  height: 30px;
  margin-left: auto;
}
@media only screen and (max-width: 767px) {
  .header__burger {
    display: inline-flex;
  }
}
.header__burger-line {
  position: relative;
  width: 100%;
  height: 2px;
  background-color: #46cfff;
  border-radius: 10px;
}
.header__burger-line::before, .header__burger-line::after {
  position: absolute;
  left: 0;
  display: block;
  height: 2px;
  background-color: #46cfff;
  border-radius: 10px;
  content: "";
}
.header__burger-line::before {
  top: -6px;
  width: 75%;
}
.header__burger-line::after {
  bottom: -6px;
  width: 50%;
}

.footer {
  position: relative;
  z-index: 10;
  padding: 68px 0;
  border-radius: 40px 40px 0 0;
  background-color: #021f29;
  margin-top: 80px;
}
@media only screen and (max-width: 767px) {
  .footer {
    margin-top: 60px;
  }
}
.footer__container {
  display: flex;
  align-items: center;
